<template>
  <div>
    <oct-base-menu></oct-base-menu>
    <!--<v-touch v-on:swipeleft="onSwipeLeft" v-on:swiperight="onSwipeRight" tag="div" style="touch-action: pan-y!important;">-->
      <transition :name="transitionName" :mode="mode">
        <keep-alive v-if="$route.meta.keepAlive" >
          <router-view class="Router">
          </router-view>
        </keep-alive>
        <router-view v-else class="Router"></router-view>
      </transition>
    <!--</v-touch>-->
  </div>
</template>

<script>
import OctBaseMenu from '@/components/baseLayout/OctBaseMenu'

export default {
  name: 'AppHome',
  components: { OctBaseMenu },
  data () {
    return {
      transitionName: 'slide-right',
      mode: 'out-in'
    }
  },
  beforeDestroy () {
  },
  methods: {
    onSwipeLeft () {
      this.mode = 'out-in'
      this.transitionName = 'slide-left'
      let path = this.$route.path
      let to = ''
      if (path === '/plan') {
        to = '/user'
      }
      if (path === '/record') {
        to = '/plan'
      }
      if (path === '/user') {
        to = '/record'
      }
      this.$router.push(to)
    },
    onSwipeRight () {
      this.mode = 'in-out'
      this.transitionName = 'slide-right'
      this.$router.isBack = true
      let path = this.$route.path
      let to = ''
      if (path === '/plan') {
        to = '/record'
      }
      if (path === '/record') {
        to = '/user'
      }
      if (path === '/user') {
        to = '/plan'
      }
      this.$router.push(to)
    }
  }

}
</script>

<style scoped>
  .Router {
    transition: all .4s ease;
    min-height: 100vh;
  }

  .slide-left-enter,
  .slide-right-leave-active {
    opacity: 0;
    -webkit-transform: translate(100%, 0);
    transform: translate(100%, 0);
  }

  .slide-left-leave-active,
  .slide-right-enter {
    opacity: 0;
    -webkit-transform: translate(-100%, 0);
    transform: translate(-100% 0);
  }
</style>
