  <van-tabbar
    style="background-color: rgb(248, 246, 242)"
    route
    :safe-area-inset-bottom="true"
    v-model="active"
    active-color="rgb(165, 145, 108)"
    inactive-color="rgb(125, 125, 125)"
  >
    <van-tabbar-item icon="clock" to="/record">打卡</van-tabbar-item>
    <van-tabbar-item icon="todo-list" to="/plan">计划单</van-tabbar-item>
    <van-tabbar-item icon="manager" to="/user">个人中心</van-tabbar-item>
  </van-tabbar>

</template>

<script>
export default {
  name: 'OctBaseMenu',
  data () {
    return {
      active: 'record'
    }
  },
  watch: {
    $route (to, from) {
      if (to !== undefined) {
        switch (to.path) {
          case '/record':
            this.active = 'record'
            break
          case '/plan':
            this.active = 'plan'
            break
          case '/user':
            this.active = 'user'
            break
          default:
            this.active = 'record'
        }
      }
    }
  },
  mounted () {
    if (this.active !== this.$route.path) {
      this.active = this.$route.path
      this.active = this.active.replace('/', '')
    }
  },
  methods: {
  }
}
</script>

